;\n    }\n    &::after{\n      background-color: #fff;\n    }\n  }\n}\n\n.abc-checkbox{\n  cursor: default;\n  padding-left: 4px;\n\n  label{\n    cursor: pointer;\n    display: inline;\n    vertical-align: top;\n    position: relative;\n    padding-left: 5px;\n\n    &::before{\n      cursor: pointer;\n      content: \"\";\n      display: inline-block;\n      position: absolute;\n      width: 17px;\n      height: 17px;\n      top: 2px;\n      left: 0;\n      margin-left: -$form-check-input-gutter;\n      border: 1px solid $input-border-color;\n      border-radius: 3px;\n      background-color: #fff;\n      @include transition(border 0.15s ease-in-out, color 0.15s ease-in-out);\n    }\n\n    &::after{\n      cursor: pointer;\n      display: inline-block;\n      position: absolute;\n      width: 16px;\n      height: 16px;\n      left: 0;\n      top: 2px;\n      margin-left: -$form-check-input-gutter;\n      padding-left: 3px;\n      padding-top: 1px;\n      font-size: 11px;\n      color: $input-color;\n    }\n  }\n\n  input[type=\"checkbox\"],\n  input[type=\"radio\"] {\n    position: static;\n    margin-left: 0;\n    cursor: pointer;\n    opacity: 0;\n    z-index: 1;\n\n    &:focus + label::before{\n      outline: thin dotted;\n      outline: 5px auto -webkit-focus-ring-color;\n      outline-offset: -2px;\n    }\n\n    &:checked + label::after{\n      font-family: $font-family-icon;\n      content: $check-icon;\n    }\n\n    &:indeterminate + label::after{\n      display: block;\n      content: \"\";\n      width: 10px;\n      height: 3px;\n      background-color: #555555;\n      border-radius: 2px;\n      margin-left: -16.5px;\n      margin-top: 7px;\n    }\n\n    &:disabled + label{\n      opacity: 0.65;\n\n      &::before{\n        background-color: $input-disabled-bg;\n        cursor: not-allowed;\n      }\n\n      &::after{\n        cursor: not-allowed;\n      }\n    }\n\n  }\n\n  &.abc-checkbox-circle label::before{\n    border-radius: 50%;\n  }\n\n  &.checkbox-inline{\n    margin-top: 0;\n  }\n}\n\n@include checkbox-variant('.abc-checkbox-primary', theme-color(primary));\n@include checkbox-variant('.abc-checkbox-danger', theme-color(danger));\n@include checkbox-variant('.abc-checkbox-info', theme-color(info));\n@include checkbox-variant('.abc-checkbox-warning', theme-color(warning));\n@include checkbox-variant('.abc-checkbox-success', theme-color(success));\n\n\n@include checkbox-variant-indeterminate('.abc-checkbox-primary', theme-color(primary));\n@include checkbox-variant-indeterminate('.abc-checkbox-danger', theme-color(danger));\n@include checkbox-variant-indeterminate('.abc-checkbox-info', theme-color(info));\n@include checkbox-variant-indeterminate('.abc-checkbox-warning', theme-color(warning));\n@include checkbox-variant-indeterminate('.abc-checkbox-success', theme-color(success));\n\n//\n// Radios\n// --------------------------------------------------\n\n@mixin radio-variant($parent, $color) {\n  #{$parent} input[type=\"radio\"]{\n    + label{\n      &::after{\n        background-color: $color;\n      }\n    }\n    &:checked + label{\n      &::before {\n        border-color: $color;\n      }\n      &::after{\n        background-color: $color;\n      }\n    }\n  }\n}\n\n.abc-radio{\n  cursor: default;\n  padding-left: 4px;\n\n  label{\n    cursor: pointer;\n    display: inline;\n    vertical-align: top;\n    position: relative;\n    padding-left: 5px;\n\n    &::before{\n      cursor: pointer;\n      content: \"\";\n      display: inline-block;\n      position: absolute;\n      width: 17px;\n      height: 17px;\n      top: 2px;\n      left: 0;\n      margin-left: -20px;\n      border: 1px solid $input-border-color;\n      border-radius: 50%;\n      background-color: #fff;\n      @include transition(border 0.15s ease-in-out);\n    }\n\n    &::after{\n      cursor: pointer;\n      display: inline-block;\n      position: absolute;\n      content: \" \";\n      width: 11px;\n      height: 11px;\n      left: 3px;\n      top: 5px;\n      margin-left: -20px;\n      border-radius: 50%;\n      background-color: $input-color;\n      transform: scale(0, 0);\n\n      transition: transform .1s cubic-bezier(.8,-0.33,.2,1.33);\n      //curve - http://cubic-bezier.com/#.8,-0.33,.2,1.33\n    }\n  }\n\n  input[type=\"radio\"]{\n    position: static;\n    margin-left: 0;\n    cursor: pointer;\n    opacity: 0;\n    z-index: 1;\n\n    &:focus + label::before{\n      outline: thin dotted;\n      outline: 5px auto -webkit-focus-ring-color;\n      outline-offset: -2px;\n    }\n\n    &:checked + label::after{\n      transform: scale(1, 1);\n    }\n\n    &:disabled + label{\n      opacity: 0.65;\n\n      &::before{\n        cursor: not-allowed;\n      }\n\n      &::after{\n        cursor: not-allowed;\n      }\n    }\n\n  }\n\n  &.radio-inline{\n    margin-top: 0;\n  }\n}\n\n@include radio-variant('.abc-radio-primary', theme-color(primary));\n@include radio-variant('.abc-radio-danger', theme-color(danger));\n@include radio-variant('.abc-radio-info', theme-color(info));\n@include radio-variant('.abc-radio-warning', theme-color(warning));\n@include radio-variant('.abc-radio-success', theme-color(success));\n\n\n// Validation states\n// TODO: keep in sync with bootstrap since strange comment 'redo check markup lol crap'\n.form-check-input {\n  .was-validated &:invalid,\n  &.is-invalid {\n    .abc-checkbox,\n    .abc-radio {\n      label &:before {\n        border-color: $form-feedback-invalid-color;\n      }\n    }\n  }\n}\n","/**\n * Custom application mixins available through out the app\n */\n\n// define sass mixin\n// Generates single property classes\n// -------------------------\n@mixin property-variants($base, $property, $variants: ()) {\n  @each $size, $value in $variants {\n    @if $size == 'md' {\n      #{$base} {\n        #{$property}: $value;\n      }\n\n      #{$base}-n {\n        #{$property}: $value;\n      }\n    }\n\n    #{$base}-#{$size} {\n      #{$property}: $value;\n    }\n\n    #{$base}-n-#{$size} {\n      #{$property}: -$value;\n    }\n  }\n}\n\n// Common thumbnail properties\n// -------------------------\n@mixin thumb($parent, $size) {\n  #{$parent} {\n    width: $size;\n\n    > .glyphicon,\n    > .fa {\n      line-height: $size;\n    }\n  }\n}\n\n@mixin border-radius($radius) {\n  border-radius: $radius;\n}\n\n@mixin scroll-bar($color) {\n  &::-webkit-scrollbar {\n    height: 8px;\n    width: 4px;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    border: none;\n    background-color: $color;\n  }\n}\n\n@mixin keyframes($name) {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n\n  @keyframes #{$name} {\n    @content;\n  }\n}\n",".page-title {\n  margin: 0 0 calc(1rem + 6px);\n}\n\n.input-group.focus {\n  .input-group-addon,\n  input {\n    background-color: #f1f1f1;\n  }\n}\n\n.animated {\n  animation-duration: 0.5s;\n}\n\n.chat-notification-sing {\n  position: absolute;\n  top: 12px;\n  left: 14px;\n  display: inline-block;\n  width: 8px;\n  height: 8px;\n  border-radius: 50%;\n  background-color: theme-color('danger');\n}\n\n#notification-buttons {\n  display: flex;\n\n  button {\n    flex: 1;\n    font-family: $font-family-base;\n    font-size: 0.875rem;\n    background-color: #f8f8f8;\n\n    &.active {\n      color: #343434;\n      background-color: #dfdfdf;\n      background-image: none;\n      border-color: #adadad;\n      box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.12);\n    }\n  }\n}\n\n.btn-toolbar .btn {\n  margin-left: 0.5rem;\n}\n\n/* --------- Forms --------- */\n.form-action {\n  background-color: #f5f5f5;\n  margin: $spacer 0 0;\n  padding: $spacer/2 $spacer;\n}\n\n.widget-top-overflow,\n.widget-middle-overflow {\n  position: relative;\n  margin: 0 (-$widget-padding-horizontal);\n\n  > img {\n    max-width: 100%;\n  }\n}\n\n.widget-top-overflow {\n  margin-top: 2 * (-$widget-padding-vertical);\n  border-top-left-radius: $border-radius;\n  border-top-right-radius: $border-radius;\n  overflow: hidden;\n\n  > img {\n    border-top-left-radius: $border-radius;\n    border-top-right-radius: $border-radius;\n  }\n\n  > .btn {\n    position: absolute;\n    top: 0;\n    right: 0;\n    z-index: 1;\n    margin-right: $widget-padding-horizontal;\n\n    @include media-breakpoint-up(md) {\n      top: auto;\n      bottom: 0;\n    }\n  }\n}\n\n.widget-table-overflow {\n  margin: 0 (-$widget-padding-horizontal) (-$widget-padding-vertical);\n\n  th:first-child,\n  td:first-child {\n    padding-left: $widget-padding-horizontal;\n  }\n\n  th:last-child,\n  td:last-child {\n    padding-right: $widget-padding-horizontal;\n  }\n}\n\n.table-bordered th {\n  background-color: $gray-300;\n}\n\ntable > thead > tr > th {\n  border-top: none !important;\n  border-bottom-width: 1px !important;\n  text-transform: uppercase;\n}\n\n/* --------- Pagination --------- */\n.pagination {\n  font-weight: $font-weight-normal;\n\n  > .page-item {\n    display: inline; // Remove list-style and block-level defaults\n\n    > .page-link,\n    > span {\n      border-radius: $border-radius;\n      margin: 0 2px;\n    }\n\n    > .page-link {\n      text-decoration: none;\n      border: none;\n    }\n  }\n}\n\n/* --------- Stats Row --------- */\n.stats-row {\n  margin-bottom: 1.25rem;\n}\n\n.stat-item {\n  display: inline-block;\n  padding-right: 1.25rem;\n\n  & + .stat-item {\n    padding-left: 1.25rem;\n    border-left: 1px solid $hr-border-color;\n  }\n\n  .name {\n    margin-bottom: 0.25rem;\n    font-size: $font-size-sm;\n  }\n\n  &.stat-item-mini-chart {\n    position: relative;\n    top: -1rem;\n    padding-left: 0;\n    border-left: none;\n  }\n}\n\n/*         Widgets Grid          */\n\n.widget-container {\n  min-height: 30px;\n\n  .widget:not(.fullscreened) > header {\n    cursor: move;\n  }\n}\n\n.widget-placeholder {\n  background: rgba($white, 0.4);\n  border: 1px dashed $gray-600;\n  margin: -1px -1px calc(#{$grid-gutter-width} - 1px) -1px;\n}\n\n/*         Post Links           */\n\n.post-links {\n  margin-bottom: 0;\n  font-size: $font-size-sm;\n  padding-left: 0;\n\n  @include clearfix();\n\n  > li {\n    float: left;\n    list-style: none;\n\n    + li {\n      &::before {\n        color: #999;\n        content: '\\25cf';\n        padding: 0 8px;\n      }\n    }\n\n    > a {\n      text-decoration: none;\n      color: $text-muted;\n\n      &:hover {\n        color: $text-muted;\n      }\n    }\n  }\n\n  &.no-separator > li + li {\n    margin-left: 12px;\n\n    &::before {\n      content: normal;\n    }\n  }\n}\n\n/*          Post Comments           */\n\n.post-comments {\n  margin-bottom: $spacer / 2;\n  margin-left: -20px;\n  margin-right: -20px;\n  font-size: $font-size-sm;\n\n  @include clearfix();\n\n  > li {\n    padding: 10px;\n    border-top: 1px solid #e7e7e7;\n    list-style: none;\n\n    @include clearfix();\n  }\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n\n  .avatar {\n    margin-top: 1px;\n  }\n\n  .author {\n    margin-top: 0;\n    margin-bottom: 2px;\n    color: #7ca9dd;\n  }\n\n  .comment-body {\n    overflow: auto;\n  }\n\n  h6.author > small {\n    font-size: 11px;\n  }\n\n  .widget > footer & {\n    margin-left: -$widget-padding-horizontal;\n    margin-right: -$widget-padding-horizontal;\n  }\n}\n",".container-fluid {\n  padding-left: 30px;\n  padding-right: 30px;\n}\n\n.navbar {\n  height: $navbar-height;\n  border: none;\n  font-weight: 500;\n  justify-content: flex-start;\n\n  .nav {\n    height: 100%;\n  }\n\n  .nav-item {\n    .nav-link {\n      display: flex;\n      align-items: center;\n      color: $navbar-link-color;\n      height: 100%;\n      position: relative;\n\n      @include hover {\n        color: $navbar-link-hover-color;\n        background: $navbar-link-hover-bg;\n      }\n\n      .la {\n        font-size: 20px;\n      }\n    }\n  }\n\n  .nav > .nav-item > .nav-link.bs-tether-target.show {\n    display: flex !important;\n  }\n\n  .input-group {\n    width: 245px;\n  }\n\n  .form-control {\n    line-height: 1.7;\n    padding: 0.6rem 0.85rem;\n  }\n\n  .dropdown-menu {\n    margin: 0;\n  }\n\n  @include media-breakpoint-down(sm) {\n    font-size: $font-size-lg;\n  }\n}\n\n.progress-bar {\n  @include box-shadow(none);\n}\n\n.progress {\n  height: $spacer * 1.3;\n  margin-bottom: $spacer / 2;\n}\n\n.progress-sm {\n  height: 10px;\n}\n\n.progress-xs {\n  height: 5px;\n}\n\n.form-control {\n  font-family: $font-family-base;\n  box-shadow: none;\n  transition: border-color ease-in-out 0.15s, background-color ease-in-out 0.15s;\n\n  &:focus {\n    box-shadow: none;\n  }\n\n  &.no-border {\n    border: none;\n    background-color: darken($input-bg, 5%);\n\n    &:focus {\n      background-color: darken($input-bg, 7%);\n    }\n  }\n}\n\nlegend {\n  display: block;\n  width: 100%;\n  padding: 0;\n  margin-bottom: $spacer;\n  line-height: inherit;\n  color: $text-muted;\n  border: 0;\n  border-bottom: 1px solid #e5e5e5;\n  font-size: 15px;\n  font-weight: $font-weight-normal;\n}\n\n.input-sm {\n  font-size: 13px;\n}\n\n.alert {\n  font-size: $font-size-sm;\n\n  .close {\n    color: rgba(#000, 0.4);\n  }\n}\n\n.alert-sm {\n  padding: 10px 15px;\n\n  .close {\n    font-size: 20px;\n    top: 0;\n    right: -8px;\n  }\n}\n\n/*  Badge */\n// Add colors for badges\n.badge {\n  padding: 4px;\n  font-weight: $font-weight-bold;\n  font-size: 11px;\n}\n\n.badge-success,\n.badge-info,\n.badge-warning,\n.badge-danger {\n  color: $white;\n}\n\n.badge-white {\n  @include badge-variant($white);\n}\n\n.badge-default {\n  @include badge-variant($gray-300);\n}\n\n.badge {\n  &.badge-pill {\n    padding: 0.4em 0.6em;\n    font-weight: 700;\n  }\n\n  .list-group-item > & {\n    margin-top: 2px;\n  }\n}\n\n.table-no-border {\n  margin-left: -$table-cell-padding;\n  margin-right: -$table-cell-padding;\n\n  > thead > tr > th,\n  > thead > tr > td,\n  > tbody > tr > th,\n  > tbody > tr > td,\n  > tfoot > tr > th,\n  > tfoot > tr > td {\n    border-top: 0;\n  }\n}\n\n.table-sm {\n  font-size: $font-size-sm;\n\n  > thead > tr > th,\n 